/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CInputFile,
  CDataTable,
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CAlert,
  CSelect,
} from '@coreui/react'
import ModalForm from '../../../../components/ModalForm/ModalForm';
import ModalWarning from '../../../../components/ModalWarning/ModalWarning';
import {colors} from '../../coursesCategories/colors';
import {useLocation} from 'react-router-dom';

import {getCategories, deleteCategory, createCategory, updateCategory} from '../../../../services/categoriesService';
import {getModules, createModule, updateModule, deleteModule} from '../../../../services/modulesService';

const getBadge = highlight => {
  switch (highlight) {
    case true: return 'success'
    case false: return 'danger'
    default: return 'primary'
  }
}
const fields = [ 'order_number',{key: 'name', value: 'nombre', sorter: true}, 'description', 'url','actions']
const fields1 = {order_number: 'Orden', name:'Nombre',  description: 'Descripcion',  url: 'Enlace', actions: 'Acciones'}
const columnsFilter = { name:'Nombre', description: 'Descripcion', order_number: 'Orden', url: 'Enlace' }
// const fields2 = ['name']

const CourseModules = ({history}) => {
  const location = useLocation();
  const [visibleForm, setVisibleForm] = useState(false);
  const [data, setData] = useState([]);
  const [deleteRow, setDeleteRow] = useState(false);
  const [isEditing, setEditing] = useState(false)
  const [error, setError] = useState(false)
  const [errorFile, setErrorFile] = useState(false);
  const [idForDelete, setIdForDelete] = useState(null);
  const [loading, setLoading] = useState(null);
  const [values, setValues] = useState({
    id: '',
    course: location.id,
    name: '',
    description: '',
    order_number: '',
    url: '',
    image: '',
  });
  const toggle = ()=>{
    setValues({...values,
        id: '',
        name: '',
        description: '',
        order_number: '',
        url: '',
        image: '',
        clean: visibleForm ? true : false
    })
    setVisibleForm(!visibleForm);
    setError(false);
    setErrorFile(false);
    setEditing(false);
}
 useEffect (() => {
    //  alert(values.course)
  handleLogout();
  getData();
},[])


const handleLogout = async () => {
    const token =  await localStorage.getItem('apitoken');
    if(token===null || token === undefined){
      history.push('/login');
    }
   }

 const getData = async () => {
   try {
    setLoading(true);
    const response = await getModules(values.course);
    await setData(response[0].module_list.reverse());
    console.log(response);
    if(response[0].module_list.length > 0) await setLoading(false);
    else await setLoading(false)
   } catch(err){
     console.log(err)
   }
 }

 const storeModule = async () => {
    let data = new FormData();
    if(values.name==='' || values.description==='' || values.url==='' || values.order_number==='' || values.image===''){
        setError(true)
    }else{
        data.append('course', values.course);
        data.append('name', values.name);
        data.append('description', values.description);
        data.append('url', values.url);
        data.append('order_number', values.order_number);
        data.append('image', values.image);
    try {
        const response = await createModule(data)
        if(response) {
            console.log(response.data, 'modulo creado');
            setVisibleForm(!visibleForm);
            await getData();
            setError(false)
        }
    } catch(err){
        console.log(err)
    }
}
 }

 const deleteItem = (id) => {
    setDeleteRow(!deleteRow);
    setIdForDelete(id)
  }

  const handleDelete = async (id) => {
     console.log(id,'si entra al delete')
     try {
    if(id!==null){
    await deleteModule(id)
  }
    } catch (err) {
        console.log(err);
    }
    setDeleteRow(!deleteRow);
    await getData();
  }

  const handleEdit = (item) => {

    setVisibleForm(!visibleForm);
    setEditing(true);
    setError(false);
    setValues({...values,
        id: item.id,
        course: values.course,
        name: item.name,
        description: item.description,
        url: item.url,
        order_number: item.order_number,
        clean: visibleForm ? true : false
      })

  }

  const updateMod = async () => {
    let data = new FormData();
    data.append('course', values.course);
    data.append('name', values.name);
    data.append('description', values.description);
    data.append('url', values.url);
    data.append('order_number', values.order_number);
    if(values.image!=='')data.append('image', values.image);

    if(values.course !== '' && values.name!=='' && values.description!=='' && values.url!==''  && values.order_number!=='' ){
          try {
            const response = await updateModule(data, values.id)
            if(response) {
                console.log(response.data, 'usuario creado');
                setVisibleForm(!visibleForm);
                await getData();
                setEditing(false);
            }
          } catch(err) {
              console.log(err);
          }
      } else {
          setError(true);
      }
  }

  const handleChange = (e) => {
    e.preventDefault();
    setValues({...values,
      [e.target.name] : e.target.value
    })
    console.log(values);

  }

  const changeFile = (e) => {
    var file = e.target.files[0],
        pattern = /^image/,
        reader = new FileReader();

        if(!pattern.test(file.type)) {
            setErrorFile(true);
            console.log(e.target.files[0].name)
            // eslint-disable-next-line no-unreachable
            setValues({...values,
              image: '',
              clean: visibleForm ? true : false,
            })
            return;
        } else{
          setErrorFile(false);

    reader.onload = upload => {
    setValues({...values,
    image: upload.target.result
    });

    console.log(values);
  }
  reader.readAsDataURL(file);
      console.log(values.image);
      console.log("Uploaded");

  console.log(values.image);
        }
  }

  return (
    <>
      <CCard>
        <CCardHeader>
          {/* <DocsLink href="https://coreui.io/docs/utilities/colors/"/> */}
          <CButton
          color="primary"

      onClick={() => toggle()}
      className="mr-1"
    >Agregar Módulo</CButton>
        </CCardHeader>
        <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Módulos de {location.course}
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={data}
              fields={fields}
              tableFilter={{label: 'Buscar', placeholder: 'Buscar Modulo'}}
              itemsPerPage={10}
              columnHeaderSlot={fields1}
              loading={loading}
              pagination
              striped
              responsive
              scopedSlots = {{
                'name':
            (item) => (
            <td style={{maxHeight:20, height: 20, maxWidth: 120}}>{`${item.name}`}</td>
            ),
            'description':
            (item) => (
            <td style={{maxHeight:20, height: 20, maxWidth: 150}}>{`${item.description}`}</td>
            ),
              'url':
              (item) => (
                  <td>
              <a href={item.url}>{item.url}</a>
              </td>
              ),
                'actions':
            (item) => (
              <td>
                <CButton style={{marginRight: 10}} onClick={() => handleEdit(item)} color="warning">Editar</CButton>
                <CButton onClick={() => deleteItem(item.id)} color="danger">Eliminar</CButton>
              </td>
            ),

            }
          }
            />
            </CCardBody>
          </CCard>
        </CCol>
      </CCard>
      <ModalForm visible={visibleForm} modalToggle={toggle} onSubmit={!isEditing ? () => storeModule() : () => updateMod()} title="Módulo" >
      <CCol xs="12" sm="12">
          <CCard>
            <CCardBody>
              <CFormGroup>
                <CLabel htmlFor="name">Nombre</CLabel>
                <CInput required id="name" name="name" value={values.name} onChange={(e) => handleChange(e)} placeholder="Nombre del módulo" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="description">Descripción</CLabel>
                <CInput id="description" name="description" value={values.description} onChange={(e) => handleChange(e)} placeholder="Descripción" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="order_number">Orden</CLabel>
                <CInput type="number" id="order_number" name="order_number" value={values.order_number} onChange={(e) => handleChange(e)} placeholder="Orden" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="url">Url</CLabel>
                <CInput id="url" name="url" value={values.url} onChange={(e) => handleChange(e)} placeholder="Url" />
              </CFormGroup>
              <CFormGroup key={values.clean} row>
                  <CLabel col  htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="12">
                    <CInputFile encType="multipart/form-data" onChange={(e) => changeFile(e)} id="image" name="image"/>
                  </CCol>
                </CFormGroup>
              { error ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Debes ingresar todos los campos!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                { errorFile ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Formato de archivo inválido, solo imágenes!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                {/* <CFormGroup row>
                  <CLabel col md="3" htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="9">
                    <CInputFile id="image" name="image"/>
                  </CCol>
                </CFormGroup> */}
            </CCardBody>
          </CCard>
        </CCol>
      </ModalForm>
      <ModalWarning visible={deleteRow} onSubmit={() => handleDelete(idForDelete)} modalToggle={() => deleteItem(null)} title="Eliminar Modulo">Estas seguro de eliminar este modulo?</ModalWarning>

    </>
  )
}

export default CourseModules
